import React from 'react';
import { Table, Card } from 'antd';

const source = [
  {
    param: 'data',
    desc: '表格数据源，包含列表数据和分页数据两部分',
    type:
      ' { list : array, pagination : { current : number, pageSize : number, total : number } }',
    default: '-'
  },
  {
    param: 'loading',
    desc: '表格加载状态',
    type: 'boolean',
    default: 'false'
  },
  {
    param: 'buttons',
    desc: '表格左上方的按钮组',
    type: 'ReactNode',
    default: '-'
  },
  {
    param: 'columns',
    desc: '表格列的配置，与 antd 的 Table columns 相同',
    type: 'array',
    default: '-'
  },
  {
    param: 'onChange',
    desc: 'Table Change后的回调，通常直接传入 Collection 提供的就行',
    type: 'function',
    default: '-'
  },
  {
    param: 'selectedRows',
    desc: '已选择的 row ',
    type: 'array',
    default: '[]'
  },
  {
    param: 'onSelectRow',
    desc: '勾选 row 时的回调',
    type: 'function ( selectedRows ) ',
    default: '-'
  },
  {
    param: 'expandedRowRender',
    desc: '额外的展开层的渲染函数',
    type: 'function ( row ) : ReactNode',
    default: '-'
  },
  {
    param: 'onExpand',
    desc: '点击展开按钮（一个“+”符号）后的回调',
    type: 'function ( expand, row ) ',
    default: '-'
  }
];

const columns = [
  {
    title: '参数',
    dataIndex: 'param'
  },
  {
    title: '说明',
    dataIndex: 'desc'
  },
  {
    title: '类型',
    dataIndex: 'type'
  },
  {
    title: '默认值',
    dataIndex: 'default'
  }
];

const API = () => (
  <Card>
    <h2>StandardTable</h2>
    <Table bordered pagination={false} dataSource={source} columns={columns} />
  </Card>
);

export default API;



